<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <div class="page-content borrow">
      <div class="head">
        <div class="middle-icon"><img src="//qianka.b0.upaiyun.com/images/df050456d10b26e8174686bd0f65e564.png" alt=""></div>
        <div class="back-icon" @click="back()">
          <img src="//qianka.b0.upaiyun.com/images/d081a8fda5e6c5bbb0ea0598b8189529.png" alt="">
        </div>

        <div class="desc"><span>借1000元一天利息仅</span><img src="//qianka.b0.upaiyun.com/images/01b7582d82bd00e3cb030700bf9fa881.png" alt=""><span>元</span></div>
        <div class="desc"><span>手机＋身份证就能借，3-5分钟放款</span></div>
        <div class="progress"><img src="//qianka.b0.upaiyun.com/images/e67de21e493b052dc8ec8a9b05285a3e.png" alt=""></div>
        <div class="progress-desc">
          <div>在线申请30 秒</div>
          <div>注册下载3 分钟</div>
          <div>确认借款5 分钟</div>
        </div>
      </div>

      <div class="form" v-if="!hasBorrowed">
        <div class="borrow-input">
          <div class="title">金额（元）</div>
          <input type="tel" placeholder="借多少" class="input" v-model="form.money" maxlength="6">
          <img src="//qianka.b0.upaiyun.com/images/acf4263a8984a98cfbd6829a760b47ae.png" alt="">
          <div class="line"></div>
        </div>
        <div class="borrow-input">
          <div class="title">周期（天）</div>
          <input type="tel" placeholder="借多久" class="input" v-model="form.days" maxlength="4">
          <img src="//qianka.b0.upaiyun.com/images/acf4263a8984a98cfbd6829a760b47ae.png" alt="">
          <div class="line"></div>
        </div>
        <div class="borrow-input">
          <div class="title">借款人</div>
          <input type="text" placeholder="姓名" class="input" v-model="form.name">
          <div class="line"></div>
        </div>
        <div class="borrow-input width-code">
          <div class="title">手机号</div>
          <input type="tel" placeholder="手机号" class="input" v-model="form.phone" maxlength="11">
          <div class="code" @click="getCode()">
            <div class="seperator"></div>
            <span :class="{ disabled: isdisabled }" v-text="getCodeText" @click="getCode($event)"></span>
          </div>
          <div class="line"></div>
        </div>
        <div class="borrow-input">
          <div class="title">验证码</div>
          <input type="tel" placeholder="请输入验证码" class="input" v-model="form.code" maxlength="4">
          <div class="line"></div>
        </div>

        <div class="button button-apply" :class="{'can-submit': canSubmit}" @click="submit()">在线申请</div>
        <div class="bottom-desc">年满18周岁才可申请</div>
      </div>

      <div class="apply-success" v-if="hasBorrowed">
        <div>您的申请已提交</div>
        <div>还差2步完成借款</div>
        <div class="button button-continue" @click="goToAdvertiser()">继续</div>
        <div>年满18周岁才可申请</div>
      </div>

      <div class="bottom-info">
        借款服务由信而富提供，纽交所股票代码：XRF
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../scss/mixins";

  .borrow {
    background: #FFDD6E;
    padding-top: 40px;

    .head {
      text-align: center;

      .back-icon {
        position: absolute;
        top: 40px;
        left: 5px;
        width: 50px;
        height: 50px;

        img {
          width: 12px;
          height: 18px;
        }
      }

      .middle-icon {
        margin-bottom: 10px;

        img {
          width: 174px;
          height: 101px;
        }
      }

      .desc {
        span {
          font-size: 20px;
          color: #3A3A3A;
          height: 28px;
          line-height: 28px;
          margin-bottom: 6px;
          vertical-align: middle;

          @include font-medium();
        }

        img {
          vertical-align: middle;
          width: 25px;
          height: 18px;
        }
      }

      .progress {
        margin-top: 19px;

        img {
          width: 235px;
          height: 10px;
        }
      }

      .progress-desc {
        margin: 8px auto;
        display: flex;
        justify-content: space-between;
        width: 277px;

        div {
          width: 54px;
          height: 36px;
          display: inline-block;
          font-size: 13px;
          color: #3A3A3A;

          @include font-regular();
        }

        div:first-child {
          color: #EF4F2A;
        }
      }
    }

    .form {
      margin: 30px auto;
      width: 345px;
      height: 582px;
      background: #FFFFFF;
      border: 3px solid #3A3A3A;

      .borrow-input {
        position: relative;
        margin-left: 20px;
        margin-top: 30px;
        width: 200px;
        height: 54px;

        &.width-code {
          width: 100%;
        }

        .title {
          font-size: 12px;
          color: #B5B5B5;

          @include font-regular();
        }

        .input {
          display: inline-block;
          width: 182px;
          height: 33px;
          font-size: 24px;
          padding: 0;
          color: #3A3A3A;

          @include font-regular();

          &::-webkit-input-placeholder {
            font-size: 24px;
            color: #DDDDDD;
            padding-top: 5px;

            @include font-regular();
          }
        }

        img {
          position: absolute;
          top: 35px;
          width: 18px;
          height: 8px;
        }

        .code {
          display: inline-block;

          span {
            margin-left: 48px;
            font-size: 14px;
            color: #3A3A3A;

            @include font-regular();

            &.disabled {
              color: #c5c4c3;
            }
          }

          .seperator {
            position: absolute;
            top: 25px;
            left: 220px;
            width: 1px;
            height: 17px;
            background: #DDDDDD;
          }
        }

        .line {
          width: 200px;
          height: 1px;
          background: #DDDDDD;
        }
      }

      .button-apply {
        margin: 60px auto 20px;
        width: 172px;
        height: 40px;
        min-height: 40px;
        line-height: 40px;
        font-size: 13px;
        background: #DDDDDD;
        border-radius: 2px;
        color: #FFFFFF;
        left: 87px;

        &.can-submit {
          background: #EF4F2A;
        }

        @include font-medium();
      }

      .bottom-desc {
        text-align: center;
        font-size: 12px;
        color: #B5B5B5;

        @include font-regular();
      }
    }

    .apply-success {
      width: 352px;
      height: 262px;
      background: #FFFFFF;
      border: 3px solid #3A3A3A;
      margin: 30px auto 40px;
      text-align: center;

      div:first-child {
        margin-top: 50px;
        margin-bottom: 12px;
        font-size: 20px;
        color: #3A3A3A;

        @include font-medium();
      }

      div:nth-child(2) {
        margin-bottom: 50px;
        border-radius: 2px;
        font-size: 14px;
        color: #3A3A3A;

        @include font-regular();
      }

      .button-continue {
        width: 172px;
        height: 40px;
        line-height: 40px;
        min-height: 40px;
        background: #EF4F2A;
        border-radius: 2px;
        font-size: 13px;
        color: #FFFFFF;
        margin-bottom: 20px;

        @include font-medium();
      }

      div:last-child {
        font-size: 12px;
        color: #B5B5B5;
        margin-bottom: 20px;

        @include font-regular();
      }
    }

    .bottom-info {
      text-align: center;
      display: block;
      font-size: 12px;
      color: #3A3A3A;
      height: 17px;
      line-height: 17px;
      left: 50px;
      margin-bottom: 20px;

      @include font-regular();
    }
  }

</style>
<script>
  const countDownLimit = 60

  export default {
    data () {
      return {
        getCodeText: '获取验证码',
        getCodeLock: false,
        sec: 0,
        submitLock: false,
        canSubmit: false,
        hasBorrowed: 0,
        isdisabled: false,

        // 广告商链接
        advertiser_url: '',
        form: {
          money: '',
          days: '',
          name: '',
          phone: '',
          code: ''
        }
      }
    },

    watch: {
      'form': {
        handler: function () {
          if (this.form.money && this.form.days && this.form.name && this.form.phone && this.form.code) {
            this.canSubmit = true
          } else {
            this.canSubmit = false
          }
        },
        deep: true
      }
    },

    mounted () {
      $api.hasBorrow()
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          this.hasBorrowed = payload.is_borrowed
          this.advertiser_url = payload.advertiser_url
        })
    },

    methods: {
      back () {
        $router.back('/dashboard')
      },

      getCode () {
        // 防止按钮被快速点击两次
        if (this.getCodeLock) return
        this.getCodeLock = true

        // 处于倒计时中
        if (this.sec > 0) return

        // 校验
        let tips = ''
        if (!this.form.phone) {
          tips = '请输入手机号'
        } else if (!/^1[23456789][\d]{9}$/.test(this.form.phone)) {
          tips = '手机号格式不正确'
        }

        if (tips) {
          setTimeout(() => { this.getCodeLock = false }, 1000)
          $toast.show(tips)
          return
        }

        $api.borrowSendCode({phone: this.form.phone})
          .then(() => {
            this.sec = countDownLimit
            this.isdisabled = true
            this.countDown()
          },
          () => {
            this.getCodeLock = false
          })
      },

      countDown () {
        if (this.sec) {
          this.sec--
          this.getCodeText = this.sec ? '已发送(' + this.sec + 's)' : '发送验证码'
          if (this.sec === 0) {
            this.isdisabled = false
          }
          setTimeout(() => {
            this.countDown()
          }, 1000)
        } else {
          this.getCodeLock = false
        }
      },

      submit () {
        if (this.submitLock) {
          return false
        }

        this.submitLock = true
        let tips = ''
        let regx = new RegExp('\\s')
        let valid = false

        if (!this.form.name) {
          tips = '请输入姓名'
        } else if (regx.test(this.form.name)) {
          tips = '姓名格式不正确'
        } else if (!this.form.phone) {
          tips = '请输入手机号'
        } else if (!/^1[23456789][\d]{9}$/.test(this.form.phone)) {
          tips = '手机号格式不正确'
        } else if (!this.form.code) {
          tips = '请输入验证码'
        } else if (!/^[\d]{4}$/.test(this.form.code)) {
          tips = '验证码格式不正确'
        } else {
          valid = true
        }

        if (tips) {
          $toast.show(tips)
          setTimeout(() => { this.submitLock = false }, 500)
          if (!valid) {
            return
          }
        }

        $api.borrow(this.form)
          .then(res => res.data && res.data.payload)
          .then(payload => {
            this.submitLock = false
            this.hasBorrowed = 1
            this.goToAdvertiser()
          }, () => {
            this.submitLock = false
          })
      },

      goToAdvertiser () {
        location.href = this.advertiser_url
      }
    }
  }
</script>



